<template>
  <div class="container">
      <div id="carrito">
          <h2>Carrito</h2>
          <table class="table">
              <thead>
                  <tr>
                      <th scope="col">Nombre</th>
                      <th scope="col">Precio</th>
                      <th scope="col">Cantidad</th>
                      <th scope="col">Subtotal</th>
                  </tr>
              </thead>
              <tbody>
                  <tr v-for="(item, index) in carrito" :key="index">
                      <td>{{item.nombre}}</td>
                      <td>{{item.precioU}}</td>
                      <td>
                          {{item.cantidad}}
                          <button class="btn btn-success mx-1" @click="aumentarCarrito(item.id)">+</button>
                          <button class="btn btn-success" @click="disminuirCarrito(item.id)">-</button>
                      </td>
                      <td>{{item.subTotal}} <button class="btn btn-danger" @click="borrarCarrito(item.id)">Borrar</button> </td>
                  </tr>
                  <tr>
                      <td>Total: {{total}} MXN</td>
                      <td></td>
                      <td></td>
                      <td><b-button id="show-btn" @click="showModal">Pagar</b-button></td>
                  </tr>
              </tbody>
          </table>
          <b-modal ref="ventana-pago" hide-footer title="Confirmaci√≥n de pago">
              <div class="d-block text-center">
                  <h1>Total: {{total}} MXN</h1>
                  <input type="number" class="form-control" placeholder="Recibo MXN" v-model="recibo">
                  <p>Cambio: {{recibo-total}} MXN</p>

                  <b-button class="mx-2" variant="outline-success" block @click="pagar()">Confirmar Pago</b-button>
                  <b-button variant="outline-danger" block @click="hideModal">Cancelar</b-button>
              </div>
          </b-modal>
      </div>

      <h2>Men√∫</h2>
      <label>Mostrar:</label>
          <select class="form-control m-2" v-model="mostrar">
              <option value="Todo">Todo</option>
              <option value="Alimentos">Alimentos</option>
              <option value="Bebidas">Bebidas</option>
              <option value="Reposteria">Reposteria</option>
              <option value="OTROS">Otros</option>
          </select>
      <div id="cards">
        <div v-for="(item, index) in productos" :key="index" id="card">
            <b-card
                :title="item.nombre"
                :img-src="'https://postrescharly.com/uploads/'+item.image"
                img-top
                tag="article"
                style="max-width: 15rem;"
                class="m-2"
                v-if="item.categoria == mostrar || mostrar == 'Todo'">

                <b-card-text>{{ item.descripcion }} </b-card-text>
                <b-card-text>{{ item.precioUnitario }} MXN </b-card-text>
                <b-button variant="primary" @click="agregarCarrito(item)">A√±adir a la orden</b-button>
            </b-card>
        </div>
      </div>

  </div>
</template>

<script>

import {mapState} from 'vuex'

export default {
    data(){
        return{
            productos:[],
            totalProductos: 0,
            mostrar: 'Todo',
            
            carrito:[],
            total: 0,
            recibo: 0,
            cambio: 0
        }
    },
    computed:{
        ...mapState(["token", "usuarioDB"])
    },
    methods:{
        listarProductos(){
            let config = {
                headers:{token: this.token}
            }
            this.axios.get("producto/ver-todos", config)
                .then((res)=>{
                    this.productos = res.data.productoDB;
                    console.log(this.productos)
                })
                .catch((err)=>{ console.log(err.response) })
        },
        calcularTotal(){
            this.total = this.carrito.reduce(function(acc, num){
                return acc += num.subTotal
            },0)
        },
        agregarCarrito(item){
            const articulo = {
                id: item._id,
                nombre: item.nombre,
                existencia: item.existencia, 
                costoU: item.costoUnitario,
                precioU: item.precioUnitario,
                cantidad: 1,
                subCostoU:0,
                
                subTotal: 0
            }

            let existe = this.carrito.find(element => element.id === item._id)
            
            if(existe){
                console.log('El elemento ya existe')
                existe.cantidad += 1;
                existe.subCostoU = existe.costoU * existe.cantidad;
                existe.subTotal = existe.precioU * existe.cantidad;
                this.calcularTotal();
            }else{
                //articulo.cantidad ++;
                articulo.subCostoU = articulo.costoU * articulo.cantidad;
                articulo.subTotal = articulo.precioU * articulo.cantidad;
                this.carrito.push(articulo);
                this.calcularTotal();
            }
        },
        aumentarCarrito(id){
            let item = this.carrito.find(element => element.id === id)
            console.log(item)
            item.cantidad ++;
            item.subCostoU = item.costoU * item.cantidad;
            item.subTotal = item.precioU * item.cantidad;
            this.calcularTotal();
        },
        disminuirCarrito(id){
            let item = this.carrito.find(element => element.id === id)
            console.log(item)
            item.cantidad --;
            item.subCostoU = item.costoU * item.cantidad;
            item.subTotal = item.precioU * item.cantidad;
            this.calcularTotal();
        },
        borrarCarrito(id){
            console.log(id)
            this.carrito = this.carrito.filter(element => element.id !== id)
            this.calcularTotal();
        },

        pagar(){
            let costoTotal = this.carrito.reduce(function(acc, num){
                return acc += num.subCostoU
            },0)
            let utilidad = this.total - costoTotal;

            let venta = {
                usuarioId: this.usuarioDB.data._id,
                usuarioNombre: this.usuarioDB.data.nombre,
                orden: this.carrito,
                costoTotal,
                precioTotal: this.total,
                utilidad
            }
            
            let config = { headers: {token: this.token} }

            this.axios
                .post('/venta/nuevo', venta, config)
                .then((res)=>{
                    console.log(res.data)
                    this.carrito.splice(0, this.carrito.length);
                    this.total = 0;
                    this.recibo = 0;
                    this.cambio = 0;
                    this.hideModal();
                    alert('venta exitosa');
                    
                    
                })
                .catch((err)=>{
                    console.log(err.response)
                    alert('La venta no se concreto correctamente, favor de notificar');
                })
                .finally(() => {
                    this.listarProductos();
                })
        },

        showModal(){
            this.$refs['ventana-pago'].show()
        },
        hideModal(){
            this.$refs['ventana-pago'].hide()
        }

    },
    
    created(){
        this.listarProductos();
    }

}
</script>

<style scoped>
#cards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

article{
    border: 1px solid black;
    transition: all 300ms;
}
article:hover{
    box-shadow: 2px 2px 10px 1px black;
}

</style>